<template>
  <div>
    <div class="wrapper">
      <h1>List To do</h1>
      <input
        type="text"
        placeholder="Enter Your Work"
        v-model="item"
        @keydown.enter="addItems"
      />
      <br />
      <div v-for="(item, index) in items" :key="index">
        {{ item }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Index",

  data() {
    return {
      items: [],
      item: "",
    };
  },

  methods: {
    addItems() {
      if (this.item.length) {
        this.items.push(this.item);
        this.item = "";
      }
    },
  },

  watch: {
    items: {
      handler() {
        localStorage.setItem(JSON.stringify("items", this.items));
      },
    },
  },

  mounted() {
    if (this.items.length) {
      this.items = JSON.parse(localStorage.getItem("items"));
    }
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  width: 1000px;
  margin: 0 auto;
  background: pink;
}

@media (max-width: 768px) {
  .wrapper {
    width: 270px;
  }
}
</style>